.cell {
    background: transparent;
    border: 1px solid #999;
    float: left;
    font-size: 50px;
    font-weight: bold;
    color: red;
    line-height: 34px;
    height: 60px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 60px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.field {
    display: inline-block;
    margin-top: 100px;
}
